import { Component, OnInit, Inject } from '@angular/core';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-lyrics',
  templateUrl: './lyrics.component.html',
  styleUrls: ['./lyrics.component.scss']
})
export class LyricsComponent implements OnInit {
  public content: any;
  public parentData: any;
  public title: string;

  constructor(
    private bottomSheetRef: MatBottomSheetRef<LyricsComponent>,
    private http: HttpClient,
    @Inject(MAT_BOTTOM_SHEET_DATA) private injectedData: any) { }

  ngOnInit() {
    this.title = this.injectedData.title;
    this.http.get(this.injectedData.src, { responseType: 'text' as 'json' }).subscribe((data) => {
      this.content = data;
    });
  }

  close(event: MouseEvent): void {
    this.bottomSheetRef.dismiss();
  }

  public onLoad(event: any): void {
    console.log(event);
  }

  public onError(event: any): void {
    console.log(event);
  }
}
