import { Component, OnInit } from '@angular/core';
import { MatBottomSheet } from '@angular/material';
import { LyricsComponent } from './lyrics/lyrics.component';
import { AppService } from '../app.service';
import songList from '../../assets/lyrics-md-files/config.json';

@Component({
  selector: 'app-songbook',
  templateUrl: './songbook.component.html',
  styleUrls: ['./songbook.component.scss']
})
export class SongbookComponent implements OnInit {
  public songs: any;

  constructor(private bottomSheet: MatBottomSheet, private appService: AppService) { }

  ngOnInit() {
    this.songs = songList;
    this.appService.setHeaderTitle('Songbook');
  }

  public open(song: any) {
    console.log(song);
    const sheetRef = this.bottomSheet.open(LyricsComponent, {
      panelClass: ['fullscreen-bottomsheet'],
      data: song
    });
  }
}
